<template>
  <nav class="nav">
    <div class="header">
      <div class="flex-item toggle-button">
        <a class="toggle" v-on:click="toggleSidebar">
          <font-awesome-icon class="icon" icon="bars" />
        </a>
      </div>

      <div v-on:click="sidebarHidden = true" class="flex-item logo">
        <router-link to="/" class="logo">
          <img src="@/assets/logo-small.svg" />
        </router-link>
      </div>

      <div class="flex-item filler"></div>
    </div>

    <transition name="slide">
      <div class="sidebar" v-hammer:swipe.right="toggleSidebar" :class="{ toggled: !sidebarHidden }" v-if="!sidebarHidden">
        <div class="container">
          <ul class="pure-menu-list">
            <li class="navItem pure-menu-item">
              <a class="pure-menu-link" v-on:click="toggleDropdown">Robots<font-awesome-icon :class="{ flipped: !dropdownHidden }" class="icon flip-icon" :icon="['fas', 'angle-down']" /></a>
              <span v-on:click="sidebarHidden = true">
                <robot-dropdown class="dropdowns" :class="{ toggled: !dropdownHidden }" v-if="!dropdownHidden" />
              </span>
            </li>
            <span v-on:click="sidebarHidden = true">
              <li class="navItem pure-menu-item"><router-link class="pure-menu-link" to="/articles">Articles</router-link></li>
              <li class="navItem pure-menu-item"><router-link class="pure-menu-link" to="/contact">Contact</router-link></li>
              <li class="navItem pure-menu-item"><router-link class="pure-menu-link" to="/sponsors">Sponsors</router-link></li>
            </span>
          </ul>
        </div>
      </div>
    </transition>

    <transition name="fade">
      <div class="sidebar-background" v-if="!sidebarHidden" v-on:click="sidebarHidden = true"></div>
    </transition>
  </nav>
</template>

<script>
import eventBus from '@/events'
import RobotDropdown from './RobotDropdown.vue'

export default {
  components: {
    RobotDropdown
  },

  data () {
    return {
      sidebarHidden: true,
      dropdownHidden: true
    }
  },

  mounted () {
    eventBus.$on('mobile-navbar-toggle', this.toggleSidebar)
  },

  destroyed () {
    eventBus.$off('mobile-navbar-toggle', this.toggleSidebar)
  },

  methods: {
    toggleSidebar () {
      this.sidebarHidden = !this.sidebarHidden
    },

    toggleDropdown () {
      this.dropdownHidden = !this.dropdownHidden;
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../style/variables";

.flip-icon {
  transition: .5s ease-in-out;
}

.flipped {
  transform: rotateX(180deg);
}

.logo {
  display: flex;

  img {
    display: flex;
  }
}

.slide-leave-active,
.slide-enter-active {
  transition: .5s;
}

.slide-enter, .slide-leave-to {
  transform: translate(-100%, 0);
}

.fade-enter-active, .fade-leave-active {
  transition: opacity .5s;
}

.fade-enter, .fade-leave-to {
  opacity: 0;
}

.header {
    z-index: 4;
    position: fixed;

    width: 100vw;
    top: 0;

    height: $head-height;

    display: flex;
    flex: 1;

    align-items: center;
    justify-content: flex-start;

    .flex-item {
      display: flex;

      &.toggle-button, &.filler {
        flex: 1;
        padding-left: 20px;
      }

      &.logo {
        img {
          height: $head-logo-height;
        }

        justify-content: flex-end;
      }

      &.toggle-button {
        font-size: 2em;
        color: white;
      }
    }
}

/deep/ .sidebar {
  z-index: 3;
  position: fixed;
  top: 0;
  left: 0;

  float: left;

  background: black;
  color: white;

  height: 100vh;
  width: 80vw;

  box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.75);

  .container {
    padding: 92px 12px;
  }
  .pure-menu-link {
    &:hover, &:focus {
      // background-color: transparent;
      // color: #2A9051;
      background-color: rgb(40,40,40);
    }

    .icon {
      margin-left: 0.75em;
    }
  }

  .router-link-exact-active {
    // Temporary, but kinda want to do something so idk
    // text-decoration: underline;
    color: rgb(30, 168, 83) !important;
  }

  a {
    cursor: pointer;
  }
}

.sidebar-background {
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(5px);
  z-index: 2;

  position: fixed;
  top: 0;
  left: 0;

  height: 100vh;
  width: 100vw;
}

.header {
    background: #1b1b1b;
    width: 100%;
}
</style>
