<template>
  <ul class="dropdowns">
    <li v-for="(article, articleIndex) in robotArticles" :key="articleIndex" class="pure-menu-item"><router-link class="pure-menu-link dropdown" :to="getArticleURL(article)">{{ article.title }}</router-link></li>
  </ul>
</template>

<script>
import { articles } from '@/fake_articles.json'

export default {
  methods: {
    getArticleURL (article) {
      if (article.slug) {
        return `/article/${article.slug}`
      }
      return `/article/${article.index}`
    }
  },

  computed: {
    robotArticles () {
      return articles.map((article, index) => ({ ...article, index })).filter(article => article.tags.includes("Robots"))
    }
  }
}
</script>

